.hamburger-btn {
  position: absolute;
  top: 40%;
  right: 13%;
  border: none;
  margin: 0 auto;
  background-color: rgba(255, 255, 255, 0);
  animation-name: spin;
  animation-duration: 5s;
  outline: none;
  box-shadow: none;
}
.hamburger-btn-open {
  position: absolute;
  border: none;
  margin: 0 auto;
  right: 13%;
  top: 7%;
  background-color: white;
}
.hamburger-inner {
  display: block;
  position: absolute;
  height: 3px;
  width: 30px;
  background-color: #c4c4c4;
  margin: 0 auto;
}
.hamburger-inner::before,
.hamburger-inner::after {
  content: "";
  display: block;
  position: absolute;
  height: 3px;
  width: 30px;
  background-color: #c4c4c4;
}
.hamburger-inner::before {
  top: -10px;
}

.hamburger-inner::after {
  top: 10px;
}
.hamburger-x {
  display: none;
  position: absolute;
  height: 0;
  width: 30px;
  background-color: #c4c4c4;
  margin: 0 auto;
}
.hamburger-x::before,
.hamburger-x::after {
  content: "";
  display: block;
  position: absolute;
  height: 3px;
  width: 30px;
  background-color: #c4c4c4;
}
.hamburger-x::before {
  transform: rotate(-45deg);
}
.hamburger-x::after {
  transform: rotate(45deg);
}
