.footer {
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: white;
  color: #433f3f;
  vertical-align: middle;
  padding: 15px 0px 0px 0px;
  flex-shrink: 0;
}
.footer_top {
  text-align: center;
}
.footer_top_title {
  font-size: 22px;
}
.footer_component_organizations {
  display: flex;
  justify-content: space-around;
}
hr.footer_line {
  border-top: 1px solid #bee4ff;
  width: 103%;
}
.footer_copyright {
  position: absolute;
  color: #808a8a;
  font-size: 12px;
  padding: 15px;
}
.footer_bottom {
  display: flex;
  justify-content: center;
  width: 100%;
  color: #808a8a;
  font-size: 12px;
  padding: 15px;
}
.footer_bottom_comp {
  display: flex;
}
.dwave_icon {
  width: 18px;
  height: 18px;
}
.footer_bottom_org {
  padding: 2px 10px;
}
.href {
  color: #05558f;
  text-decoration: solid;
  font-size: 13px;
}
.blazon1 {
  width: 61px;
  height: 61px;
}
.blazon_organization {
  width: 160px;
  height: 39px;
}
.blazon2 {
  width: 161px;
  height: 37px;
}
